import React, { Component } from 'react';

import { test } from './test';

export default class Porfolio extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="portfolio">
      <div className="row">
        <div className="twelve columns collapsed">
          <h1>Check Out Some of My Works.</h1>
          <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
          {
            resumeData.portfolio && resumeData.portfolio.map((item)=>{
              return(
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a className="item-wrap" href={`${item.href}`}>
                      <img src={`${item.imgurl}`} className="item-img"/>
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>{item.name}</h5>
                          <p>{item.description}</p>
                        </div>
                      </div>
                    </a>
                  </div>
                </div>
              )
            })
          }
          </div>
        </div>
      </div>

      {/* modals */}
      <div id="modal" className="popup-modal mfp-hide">
        <img className="scale-with-grid" src="images/portfolio/switches.png" alt="" />
        <div className="description-box">
            <h4>Wwise Switches in Unreal Engine</h4>
            <p>Implements switches for footsteps in Unreal Engine given different 
            physical materials. Animation notifies are implemented in the character running animation.
            These notifies cause us to switch between sounds when stepping on different physical materials.
            </p>
          {/* <span className="categories"><i className="fa fa-tag"></i>Branding, Webdesign</span> */}
        </div>
        <div className="link-box">
            <a href="https://youtu.be/6GLERzFQC2A">Video Demo</a>
            <a href="https://github.com/oz-hernandez/Wwise_Switches/tree/master/Source/Wwise_Switches">Github Link</a>
              <a className="popup-modal-dismiss">Close</a>
        </div>
      </div>
      {/* end modals */}

  </section>
        );
  }
}